import {BaseComparator} from "@component/datagrid/comparator/base-comparator";

export class NumericComparator<T> extends BaseComparator<T> {
   constructor(private dataField: string) {
      super();
   }

   protected compareValue(a: T, b: T): number {
      return a[this.dataField] - b[this.dataField];
   }
}
