import {ClrDatagridComparatorInterface} from "@clr/angular";
import { VsanUiUtils } from '@util/vsan-util';

/**
 * Basic comparator, the child component should override the compareValue method
 * We don't expect the compared value to be json format
 */
export class BaseComparator<T> implements ClrDatagridComparatorInterface<T> {
   protected stringify(data: T, field: string): string {
      return VsanUiUtils.stringify(data, field);
   }

   protected compareValue(a: T, b: T): number {
      throw new Error("Method not implemented");
   }

   compare(a: T, b: T): number {
      return this.compareValue(a, b);
   }
}