import {Component} from "@angular/core";
import {InputProperty, OutputProperty, WizardPage} from "@component/wizard/flow-metadata";
import {VsanUiUtils} from "@util/vsan-util";
import {ShareableDatastore} from "@generated/shareable-datastore";
import {MountPrecheckTest} from "@generated/mount-precheck-test";
import {CsdMountService} from "@generated/csd-mount-service";
import {MountRemoteDatastoreContext} from "@navigation/context/mount-remote-datastore-context";
import {VsanHealthStatus} from "@generated/vsan-health-status";
import {VsanHealthUtil} from "@util/vsan-health-util";
import {Logger} from "@util/logger";
import {PageItem} from "@component/wizard/page-item";
import {PageControls} from "@component/wizard/flow-wizard.component";
import {AlertType} from "@component/validation/alert-type";
import {ScreenReaderAnnouncer} from "@service/screen-reader-announcer.service";
import {ManagedObject} from "@service/managed-object";

@WizardPage(VsanUiUtils.getString("vsan.csd.mountWizard.checkCompatibility.title"))
@Component({
   templateUrl: "check-compatibility-page.html",
   styleUrls: ["check-compatibility-page.scss"],
})
export class CheckCompatibilityPageComponent {

   getHealthIconClass: (val: VsanHealthStatus) => string = VsanHealthUtil.getHealthStatusClass;
   getHealthIconShape: (val: VsanHealthStatus) => string = VsanHealthUtil.getHealthStatusShape;

   @InputProperty()
   private pageControls: PageControls;

   @InputProperty()
   selectedDatastore: ShareableDatastore;

   @ScreenReaderAnnouncer()
   srOnlyMessage: string;

   @OutputProperty()
   get datastoreRef(): ManagedObject {
      return this.selectedDatastore.datastore.moRef;
   }

   loadingTests: boolean = true;
   hasError: boolean = false;

   precheckTests: MountPrecheckTest[];
   additionalMessages: PrecheckAdditionalMessage[];

   constructor(private csdMountService: CsdMountService) {
   }

   async ngOnInit(): Promise<void> {
      (this.pageControls as PageItem).nextButtonIsDisabled = true;
      this.getPrecheckResult();
   }

   async getPrecheckResult(): Promise<void> {
      try {
         const context: MountRemoteDatastoreContext = VsanUiUtils.getModalContext();
         this.precheckTests = await this.csdMountService.runCsdMountPrecheck(context.clusterRef,
               this.selectedDatastore.datastore.moRef);
         this.additionalMessages = this.getAdditionalMessages(this.precheckTests);
         this.updateSrOnlyMessage();
      } catch (err) {
         Logger.error("Error running the mount pre-check: " + err);
         this.pageControls.setValidationErrors([err]);
         this.hasError = true;
      } finally {
         this.loadingTests = false;
         (this.pageControls as PageItem).nextButtonIsDisabled = !this.isMountPossible();
      }
   }

   isMountPossible(): boolean {
      return !this.hasError && !this.precheckTests.some(test => test.status === VsanHealthStatus.red);
   }

   private getAdditionalMessages(precheckTests: MountPrecheckTest[]): PrecheckAdditionalMessage[] {
      return precheckTests
            .filter(test => test.reasons)
            .map(test => ({
               alerts: test.reasons,
               type: CheckCompatibilityPageComponent.getAlertType(test.status),
            }) as PrecheckAdditionalMessage);
   }

   private updateSrOnlyMessage(): void {
      try {
         const failures: number = this.additionalMessages.filter(message => message.type === AlertType.DANGER).length;
         const warnings: number = this.additionalMessages.filter(message => message.type === AlertType.WARNING).length;
         this.srOnlyMessage =
               VsanUiUtils.getString("vsan.csd.mountWizard.checkCompatibility.srOnlyMessage", failures, warnings);
      } catch (e) {
         Logger.warn("Failed to build screenreader message.");
      }
   }

   private static getAlertType(testStatus: VsanHealthStatus): AlertType {
      switch (testStatus) {
         case VsanHealthStatus.yellow:
            return AlertType.WARNING;
         case VsanHealthStatus.red:
            return AlertType.DANGER;
         default:
            return AlertType.INFO;
      }
   }
}

interface PrecheckAdditionalMessage {
   type: AlertType;
   alerts: string[];
}
