import {Filter, TreeDatagridRow} from "../tree-datagrid";
import {ClrDatagridStringFilterInterface} from "@clr/angular";

/**
 * Abstract filter class that needs to be extended in order to implement custom filtration.
 * The class has a single abstract method that takes an object of type T and returns its
 * string representation.
 */
export class BaseFilter<T> implements ClrDatagridStringFilterInterface<T>, Filter {

   accepts(data: T, search: string): boolean {
      // Handle correctly TreeDatagrid components
      if (data instanceof TreeDatagridRow) {
         data = data.data;
      }

      let label: string = this.stringify(data);
      if (label) {
         // Compare ignoring the case
         return label.toLowerCase().indexOf(search.toLowerCase()) != -1;
      } else {
         return false;
      }
   }

   match(data: T, search: string): boolean {
      return this.accepts(data, search);
   }

   protected stringify(data: T): string  {
      return data + "";
   }

}

