import {BehaviorSubject} from "rxjs";
import {ClrDatagridFilterInterface} from "@clr/angular";

/**
 * Hierarchical data provider.
 * This is a additional structure to the Clarity Datagrid that supports hierarchical data rendering.
 */
export class TreeDatagrid {

   public static forChildProperty(childField: string, rowData?: any[]) {
      return new TreeDatagrid(item => item[childField], rowData);
   }

   /**
    * The data objects that are to be rendered as first-level rows.
    */
   protected roots: any[];

   /**
    * The flat row structure consisting of the root objects and their children
    * in case the parents are expanded. Sorting and filtering are reflected here.
    */
   protected content: TreeDatagridRow[];

   /**
    * List of filters to be applied on the data.
    */
   protected filters: TreeFilter[] = [];

   /**
    * The comparator to be used to sort the items and their children.
    */
   public comparator: (item1: any, item2: any) => number;

   /**
    * Sorting direction.
    */
   public sortAscending: boolean;

   /**
    * @param childItems lambda for extracting the children of a given data object
    * @param rootItems (optional) the initial data objects
    */
   constructor(private childItems: (parent: any) => any[], rootItems?: any[]) {
      this.rootItems = rootItems;
   }

   /**
    * Removes all items.
    */
   public clear = () => this.rootItems = [];

   /**
    * Sets the given data objects as content.
    */
   public refreshGrid = (rowData: any[]) => this.rootItems = rowData;

   public set rootItems(value: any[]) {
      this.roots = value ? value : [];
      this.content = this.flatten(this.roots);
   }

   /**
    * root data objects
    */
   public get rootItems(): any[] {
      return this.roots;
   }

   /**
    * The flat row structure consisting of the root objects and their children
    * in case the parents are expanded. Sorting and filtering are reflected here.
    */
   public get rows(): TreeDatagridRow[] {
      return this.content;
   }

   /**
    * Registers a new filter in the data provider.
    * Initially the filter will accept all items.
    * The consumer can assign a predicate to the TreeFilter object
    * to add filtering rule. All registered filters' predicates are
    * evaluated using AND operator.
    */
   public registerFilter(): TreeFilter {
      let filter: TreeFilter = new TreeFilter(() => this.content = this.flatten(this.rootItems));
      this.filters.push(filter);
      return filter;
   }

   public unregisterFilter(value: TreeFilter) {
      let index: number = this.filters.indexOf(value);
      if (index != -1) {
         this.filters.splice(index, 1);
      }
   }

   /**
    * Applies sorting comparator and direction for the data provider.
    */
   public sort(comparator: (item1: any, item2: any) => number, ascending: boolean = true) {
      this.comparator = comparator;
      this.sortAscending = ascending;

      // refresh content, will expand all nodes
      this.content = this.flatten(this.rootItems);
   }

   public collapseAll = () => this.rows.forEach(r => this.collapse(r));

   public expandAll = () => this.rows.forEach(r => this.expand(r));

   protected expand(row: TreeDatagridRow) {
      if (row.isExpanded) {
         // already expanded
         return;
      }

      let rowIndex = this.content.indexOf(row);
      if (rowIndex == -1) {
         // row is filtered out
         return;
      }

      let flattenedChildren: TreeDatagridRow[] = this.flatten(this.childItems(row.data), row.depth + 1);
      if (flattenedChildren.length > 0) {
         this.content.splice(rowIndex + 1, 0, ... flattenedChildren);
      }

      row.isExpanded = true;
   }

   protected collapse(row: TreeDatagridRow) {
      if (!row.isExpanded) {
         // already collapsed
         row.isExpanded = false;
         return;
      }

      let rowIndex = this.content.indexOf(row);
      if (rowIndex == -1) {
         // row is filtered out
         return;
      }

      let removeCount: number = 0;
      for (let i = rowIndex + 1; i < this.content.length && row.depth < this.content[i].depth; i++) {
         removeCount ++;
      }

      this.content.splice(rowIndex + 1, removeCount);
      row.isExpanded = false;
   }

   // returns flattened list of the specified items and their children (and their children);
   // sorting and filter are applied;
   // items are returned in expanded state
   protected flatten(items: any[], depth: number = 0, parent?: any): TreeDatagridRow[] {
      if (!items) {
         return [];
      }

      let sortedItems: any[] = [... items];
      if (this.comparator) {
         sortedItems.sort((item1: any, item2: any) => (this.sortAscending ? 1 : -1) * this.comparator(item1, item2));
      }

      let result: TreeDatagridRow[] = [];

      for (let item of sortedItems) {

         let childItems: any[] = this.childItems(item);

         // flattened children, including their children
         let children: TreeDatagridRow[] = this.flatten(childItems, depth + 1, item);

         let filteredOut: boolean = false;
         for (let filter of this.filters) {
            // filter out items only if the filter is not applicable for the item or it's parent
            const matchFilter: boolean = filter.predicate(item) || (!!parent && filter.predicate(parent));
            if (!matchFilter) {
               filteredOut = true;
            }
         }
         if (!filteredOut || children.length > 0) {
            let row: TreeDatagridRow = new TreeDatagridRow();
            row.data = item;
            row.toggle = () => row.isExpanded ? this.collapse(row) : this.expand(row);
            row.depth = depth;
            row.isExpanded = true;
            row.isExpandable = childItems && childItems.length > 0;

            result.push(row, ... children);
         }
      }

      return result;
   }

}

/**
 * Represents a single row in a flattened tree data provider.
 */
export class TreeDatagridRow {

   /**
    * The data object for the row
    */
   public data: any;

   /**
    * Will be true if the row has children
    */
   public isExpandable: boolean;

   /**
    * Will be true if the row is currently expanded
    */
   public isExpanded: boolean;

   /**
    * Will flip the row's expanded/collapsed state
    */
   public toggle: () => void;

   /**
    * The nesting level the row is rendered at
    */
   public depth: number;

}

export interface Filter  {
   match(data: any, filter:string): boolean;
}

/**
 * Allows specifying filtering predicate for the data provider.
 * @see TreeDatagrid.registerFilter
 */
export class TreeFilter implements ClrDatagridFilterInterface<any> {

   private _predicate: (item: any) => boolean;
   changes = new BehaviorSubject<boolean>(false);

   constructor(private apply: () => void) {
      this.clear();
   }

   public clear() {
      this._predicate = (item: any) => true;
      this.apply();
   }

   public set predicate(predicate: (item: any) => boolean) {
      if (!predicate) {
         this.clear();
      } else {
         this._predicate = predicate;
         this.apply();
      }
   }

   public get predicate(): (item: any) => boolean {
      return this._predicate;
   }

   // Used by clarity filter
   isActive(): boolean {
      return this.changes.getValue();
   }

   // Used by clarity filter
   accepts(val: any): boolean {
      return true;
   }
}