import {Component, OnInit} from "@angular/core";
import {PageSet} from "@component/wizard/flow-wizard.component";
import {SelectRemoteDatastorePageComponent} from "./select-remote-datastore-page.component";
import {VsanUiUtils} from "@util/vsan-util";
import {MountRemoteDatastoreContext} from "@navigation/context/mount-remote-datastore-context";
import {CheckCompatibilityPageComponent} from "./check-compatibility-page.component";
import {CsdMountService} from "@generated/csd-mount-service";

@Component({
   selector: "vsan-mount-remote-datastore-wizard",
   template: `
      <vsan-flow-wizard
            [title]="'vsan.csd.mountWizard.title' | localized"
            [pageSets]="pages"
            [startingTaskProperty]="'mountRemoteDatastore'"
            [contextProperties]="context"
            [services]="services">
      </vsan-flow-wizard>`
})
export class MountRemoteDatastoreWizardComponent implements OnInit {
   // Get contextProperties from the modal builder in order to
   // enable opening it across different context objects (datacenters, clusters, etc.)
   context: MountRemoteDatastoreContext = VsanUiUtils.getModalContext();
   pages: PageSet[];
   services: any = [CsdMountService];

   async ngOnInit() {
      this.pages = [
         new PageSet([SelectRemoteDatastorePageComponent, CheckCompatibilityPageComponent])
      ];
   }
}
