import {Component} from "@angular/core";
import {InputProperty, OutputProperty, WizardPage} from "@component/wizard/flow-metadata";
import {VsanUiUtils} from "@util/vsan-util";
import {ShareableDatastore} from "@generated/shareable-datastore";
import {ClrDatagridSortOrder} from "@clr/angular";
import {PageControls} from "@component/wizard/flow-wizard.component";
import {ObjectFilter} from "@component/datagrid/filter/object-filter";
import {NumericComparator} from "@component/datagrid/comparator/numeric-comparator";
import {StringComparator} from "@component/datagrid/comparator/string-comparator";
import {PageItem} from "@component/wizard/page-item";

@WizardPage(VsanUiUtils.getString("vsan.csd.mountWizard.selectDatastore.title"))
@Component({
   templateUrl: "select-remote-datastore-page.html"
})
export class SelectRemoteDatastorePageComponent {

   @InputProperty()
   pageControls: PageControls;

   @InputProperty()
   availableShareableDatastores: ShareableDatastore[];

   @OutputProperty(OutputProperty.ifEmpty(VsanUiUtils.getString(
         "vsan.csd.mountWizard.selectDatastore.noSelectedDatastore")))
   selectedDatastore: ShareableDatastore;

   datastoreComparator = new StringComparator<any>("datastore.name");
   serverClusterComparator = new StringComparator<any>("serverCluster.name");
   capacityComparator = new NumericComparator<any>("capacityB");
   freeSpaceComparator = new NumericComparator<any>("freeSpaceB");
   ascSort = ClrDatagridSortOrder.ASC;

   datastoreFilter = new ObjectFilter<any>("datastore.name");
   serverClusterFilter = new ObjectFilter<any>("serverCluster.name");

   ngOnInit() {
      if (!this.availableShareableDatastores.length) {
         (this.pageControls as PageItem).nextButtonIsDisabled = true;
      }
   }
}